body {
	display:flex;
	
margin:auto;

 
  font-family: 'Lato',sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: #353232;
  color:white;

  justify-content: center;


}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

input{
  font-family: 'Lato',sans-serif;
}

.App {
  text-align: center;
}






.songContainer{
	display: flex;
	align-content: center;
	/*justify-content:space-between;*/
	padding-top: 3px;
	border-bottom: solid 1px pink;

}

.songContainer > div{
	padding-right: 20px;

}

.songTitle{
	width:40%;
}

.songPerformer{
	width:40%;
}

.deleteButton:hover{
	cursor:pointer;
	color:pink;
}
.modal {
  width: 400px;
  height:320px;
  position: absolute;
  top:0px;
  background-color: #353232;
  /*margin: 9px auto;*/
  z-index: 2;

  padding: 20px 10px;
 
  border: solid pink  2px;
  border-radius: 10px;
  
}
